






























import Vue from 'vue'
import { defineComponent, reactive, ref, computed } from "@vue/composition-api";
import { component } from 'vue/types/umd';
type Props = {
    lists: Array<String>,
    imgSrc:String,
    title:String,
    description : String
};
export default defineComponent({
    props:{
        lists:{
            type:Array
        },
        imgSrc:{
            type:String
        },
        title:{
            type:String
        },
        description:{
            type:String
        }
    },
    setup(props:Props){
        const objects = computed(()=>{
            return props.lists
        });
        const src = computed(()=>{
            return props.imgSrc
        });
        const head = computed(()=>{
            return props.title
        });
        const summary = computed(()=>{
            return props.description
        });
        return {
            objects,
            src,
            head,
            summary
        }
    },
    components:{
    }
})
